<h3></h3>
<div class="demo-block">
  <div>
  <xm-text-ellipsis :isLimitHeight="isLimitHeight" height="40" :text="text">
      <template slot="more"><span @click="show">{{isLimitHeight?'':''}}</span></template>
  </xm-text-ellipsis>
  </div>
</div>
<demo-block>
                        <div slot="desc">
</div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">text</span>: <span class="hljs-string">'101383078528101383078528101383078528'</span>,
      <span class="hljs-attr">isLimitHeight</span>:<span class="hljs-literal">true</span>
    };
  },
    <span class="hljs-attr">methods</span>:{
      show(){

       <span class="hljs-keyword">this</span>.isLimitHeight=!<span class="hljs-keyword">this</span>.isLimitHeight

