<h2>TextEllipsis </h2>
<table class="table"><thead>
<tr>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>isLimitHeighttrue</td>
<td>event</td>
</tr>
<tr>
<td>hide</td>
<td>isLimitHeighttrue</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      text: '101383078528101383078528101383078528',
      isLimitHeight:true
    };
  },
    methods:{
      show(){

       this.isLimitHeight=!this.isLimitHeight

      }


    }

};
</script>


